<template>
  <div class="search">
    <v-autocomplete v-model="from" :items="items" return-object label="From" item-title="code" variant="outlined">
      <template v-slot:item="{ props, item }">
        <v-list-item v-bind="props" :subtitle="item.raw.name" :title="item.raw.code"></v-list-item>
      </template>
    </v-autocomplete>

    <v-autocomplete v-model="to" :items="items" return-object label="To" item-title="code" variant="outlined">
      <template v-slot:item="{ props, item }">
        <v-list-item v-bind="props" :subtitle="item.raw.name" :title="item.raw.code"></v-list-item>
      </template>


    </v-autocomplete>
  </div>
</template>

<script>
export default {
  name: 'SearchComponent'
}
</script>

<script setup>
import { store } from '@/store/store';
import { ref, watch } from 'vue';
import { items } from '../assets/data';

const from = ref(null);
const to = ref(null);

watch(from, async (value) => {
  if (!value) return;

  store.commit('setFrom', [value.coordinates[0], value.coordinates[1]]);
})

watch(to, async (value) => {
  if (!value) return;

  store.commit('setTo', [value.coordinates[0], value.coordinates[1]]);
})
</script>


<style lang="scss" scoped>
.search {
  position: absolute;
  z-index: 1;
  display: flex;
  flex-flow: row nowrap;
  gap: 8px;
  margin: 16px;

  .v-input {
    min-width: 140px;
  }
}
</style>